$(".roadmap-card__btn").click(function () {
    $(".roadmap-item").removeClass("_active")
    $(this).parents(".roadmap-item").addClass("_active")
    $(".roadmap__grid").addClass("_open")
})

$(".roadmap-detail__btn").click(function () {
    $(".roadmap-item").removeClass("_active")
    $(".roadmap__grid").removeClass("_open")
})
